import articleInfo from './articleInfo.vue'
	export default {
		components:{articleInfo,},
		data() {
			return {
			loadArticleFlag:false,
			userInfo: '',
			myArticleList:[
				
				{
				articleContent: "这位同学难道不知hhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiii道阿消会爬网站吗",
				articleTitle: "阿消发威",
				commentNum: 0,
				createDate: 1578561839000,
				dislikeNum: 0,
				id: "2009CBNY5DZ91P",
				imgList: Array(1),
				isAnonymous: 0,
				isLike: false,
				likeNum: 1,
				nickname: "eddie",
				popularity: 0,
				status: 1,
				tagList: null,
				tags: "",
				userId: "oDwsO5JL9WJ4RsbeT3yjsyoLuoLU",
				viewNum: 3
				},
				
				{
				articleContent: "这位同学难道不知道阿fffffffffffffffffffffffffff消会爬网站吗",
				articleTitle: "阿消发威",
				commentNum: 0,
				createDate: 1578561839000,
				dislikeNum: 0,
				id: "200109CBNYZ91",
				imgList: Array(1),
				isAnonymous: 0,
				isLike: false,
				likeNum: 1,
				nickname: "eddie",
				popularity: 0,
				status: 1,
				tagList: null,
				tags: "",
				userId: "oDwsO5JL9WJ4RsbeT3yjsyoLuoLU",
				viewNum: 3	
				}
				,{
					articleContent: "这位同学难道不知道阿消会爬网站吗",
					articleTitle: "阿消发威",
					commentNum: 0,
					createDate: 178561839000,
					dislikeNum: 0,
					id: "200109CBNDZ91P",
					imgList: Array(1),
					isAnonymous: 0,
					isLike: false,
					likeNum: 1,
					nickname: "eddie",
					popularity: 0,
					status: 1,
					tagList: null,
					tags: "",
					userId: "oDwsO5JL9WJ4RsbeT3yjsyoLuoLU",
					viewNum: 3
				}
				
				
			],
		};
	},

	onLoad() {
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)) {
			uni.redirectTo({
				url: '../signin/signin'
			});
			return;
		} else {
			this.userInfo = userInfo; // 刷去默认值(若有)
		}

		this.mySocket.init(); // 初始化 Socket, 离线调试请注释掉
		var page = this.currentPage;
		/*this.showArticles(page);*/
	},

	methods: {
		reload: function() {
			this.showArticles(page);
		},
		// 锁
		showArticles: function(page) {
			console.log(this.loadArticleFlag);
			
			if ( this.loadArticleFlag ) {
			
			}

			this.loadArticleFlag = true;

			uni.showLoading({
				title: '加载中...'
			});
			setTimeout(() => {
				if (this.loadArticleFlag) {
					this.loadArticleFlag = false; //解锁
					uni.hideLoading();
					uni.showToast({
						title: '网络未知错误',
						icon: 'none',
						duration: 1000
					});
				}
			}, 5000); //延时五秒timeout

			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/queryPublishHistory',
				method: 'POST',
				data: {
					page: page,
					userId: that.userInfo.id,
					targetId: that.userInfo.id //应该为targetId,但缺少publicProfile的数据传输,暂时用userId测试
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
					setTimeout(() => {
						//延时加载
						uni.hideLoading();
					this.loadArticleFlag = false;

						console.log(res);
						if (page == 1) {
							that.myArticleList = [];
						}
						var newArticleList = res.data.data.rows;
						var oldArticleList = that.myArticleList;
						that.myArticleList = oldArticleList.concat(newArticleList);
						that.currentPage = page;
						that.totalPage = res.data.data.total;
						that.totalNum = res.data.data.records;
						console.log(that.totalNum);
					}, 300);
				},
				fail: res => {
					uni.hideLoading();
				this.loadArticleFlag = false;

					console.log('index unirequest fail');
					console.log(res);
				}
			});
		},
		loadMore: function() {
			var that = this;
			var currentPage = that.currentPage;
			console.log(currentPage);
			var totalPage = that.totalPage;
			console.log(totalPage);
			// 判断当前页数和总页数是否相等
			if (currentPage == totalPage) {
				// that.showArticles(1);
				uni.showToast({
					title: '没有更多文章了',
					icon: 'none',
					duration: 1000
				});
			} else {
				var page = currentPage + 1;
				that.showArticles(page);
			}
		}
	}
};
